/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-create-block-fox-duplex-block {
	color: #222;
	padding: 0;
	margin: 0;
}

* {
	box-sizing: border-box;
}

.fox-edit-controls-image-wrapper {
	padding: 10px 0;
}

.fox-img-picker-button {
	background-color: #a1d4f4;
	border-radius: 3px;
	border-width: 1px;
	font-size: 15px;
	padding: 6px 10px 8px;
}

hr.components-divider {
	border-bottom: 1px solid #bbb;
}

.fox-duplex-block-content-container {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	gap: 0;
	padding: 0;
	max-width: 1200px;
	margin-right: auto;
	margin-left: auto;
}

.fox-duplex-block-img-wrapper {
	flex: 1;
	display: flex;
	justify-content: center;
	align-items: center;
}
.fox-duplex-block-img-wrapper-padded {
	flex: 1;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 50px 40px;
}
.fox-duplex-block-img {
	max-width: 100%;
	height: auto;
}

.fox-duplex-block-img-padded {
	max-width: 100%;
	height: auto;
	border-radius: 20px;
}
.fox-duplex-block-content-wrapper {
	flex: 1;
	padding: 1rem 2rem;
	h2 {
		font-size: 4rem;
		font-weight: 700;
		line-height: 1.2;
		margin-bottom: 1rem;
		color: #222;
	}
	p {
		font-size: 1.4rem;
		line-height: 1.6;
		margin-bottom: 1rem;
		color: #222;
	}
}

.fox-duplex-block-cta-link {
	display: inline-block;
	margin-top: 1rem;
	padding: 0.5rem 1rem;
	background-color: #0073aa;
	color: #fff;
	// font-weight: 700;
	text-decoration: none;
	border-radius: 4px;
}
.fox-duplex-block-cta-link:hover {
	background-color: #ddf;
	color: #0073aa;
	border: 1px solid #0073aa;
}

.fox-duplex-block-container {
	background-color: #bef;
	min-height: 350px;
	color: #777;
}

.fox-duplex-block-container-mob {
	display: none;
	background-color: #bef;
	// min-height: 600px;
	color: #777;
}

.fox-duplex-block-content-wrapper {
	max-width: 690px;
	padding-top: 60px;
	padding-bottom: 100px;
	color: #777;
}

.fox-duplex-block-container-mob {
	.fox-duplex-block-content-container {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		gap: 0;
	}
	.fox-duplex-block-content-wrapper {
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;
		padding-top: 30px;
		padding-bottom: 60px;
	}
}

@media screen and (max-width: 900px) {
	.fox-duplex-block-container-mob {
		display: block;
	}

	.fox-duplex-block-container {
		display: none;
	}
}

@media screen and (max-width: 782px) {
	.fox-duplex-block-container h2,
	.fox-duplex-block-container-mob h2 {
		font-size: 3rem;
	}
}

@media screen and (max-width: 600px) {
	.fox-duplex-block-content-container {
		flex-direction: column;
	}
	.fox-duplex-block-img-wrapper,
	.fox-duplex-block-img-wrapper-padded,
	.fox-duplex-block-content-wrapper {
		flex: none;
		width: 100%;
	}
	.fox-duplex-block-content-wrapper {
		padding: 1rem;
	}
	.fox-duplex-block-img-wrapper-padded {
		padding: 25px 15px;
	}
}
